import React from 'react';
import PropTypes from 'prop-types';
import '../css/Gnav.css';

const Gnav = ({ tab, onTabChange }) => {
	let classes = {
		Stat: '',
		Report: '',
		List: '',
		FileReader: ''
	};
	switch (tab) {
	case 'Report':
		classes.Report = 'Active';
		break;

	case 'List':
		classes.List = 'Active';
		break;

	case 'FileReader':
		classes.FileReader = 'Active';
		break;

	default:
		classes.Stat = 'Active';
		break;
	}
	return (
		<nav className="Gnav">
			<ul>
				<li
					className={classes.Stat}
					onClick={() => {
						onTabChange('Stat');
					}}
				>
					Stat
				</li>
				<li
					className={classes.Report}
					onClick={() => {
						onTabChange('Report');
					}}
				>
					
				</li>
				<li
					className={classes.List}
					onClick={() => {
						onTabChange('List');
					}}
				>
					
				</li>
				<li
					className={classes.FileReader}
					onClick={() => {
						onTabChange('FileReader');
					}}
				>
					
				</li>
			</ul>
		</nav>
	);
};

Gnav.propTypes = {
	tab: PropTypes.string.isRequired,
	onTabChange: PropTypes.func.isRequired
};

export default Gnav;



