exports.template = [
	{
		label: '',
		submenu: [
			{ role: 'undo', label: '' },
			{ role: 'redo', label: '' },
			{ type: 'separator' },
			{ role: 'cut', label: '' },
			{ role: 'copy', label: '' },
			{ role: 'paste', label: '' },
			{ role: 'delete', label: '' },
			{ role: 'selectall', label: '' }
		]
	},
	{
		label: '',
		submenu: [
			{ role: 'reload', label: '' },
			{ role: 'forcereload', label: '' },
			{ role: 'toggledevtools' },
			{ type: 'separator' },
			{ role: 'resetzoom', label: '' },
			{ role: 'zoomin', label: '' },
			{ role: 'zoomout', label: '' },
			{ type: 'separator' },
			{ role: 'togglefullscreen', label: '' }
		]
	},
	{
		label: '',
		submenu: [
			{ role: 'minimize', label: '' },
			{ role: 'close', label: '' }
		]
	},
	{
		label: '',
		submenu: [
			{ role: 'about', label: '' },
			{
				label: 'Electron',
				click() {
					require('electron').shell.openExternal('https://electron.atom.io');
				}
			}
		]
	}
];

exports.darwinTemplate = {
	main: {
		label: 'TAMentorConsole',
		submenu: [
			{ role: 'about', label: '' },
			{ type: 'separator' },
			{ role: 'services', label: '', submenu: [] },
			{ type: 'separator' },
			{ role: 'hide', label: '' },
			{ role: 'hideothers', label: '' },
			{ role: 'unhide', label: '' },
			{ type: 'separator' },
			{ role: 'quit', label: '' }
		]
	},
	sub1: [
		{ type: 'separator' },
		{
			label: '',
			submenu: [
				{ role: 'startspeaking', label: '' },
				{ role: 'stopspeaking', label: '' }
			]
		}
	],
	sub2: [
		{ role: 'close', label: '' },
		{ role: 'minimize', label: '' },
		{ role: 'zoom', label: '' },
		{ type: 'separator' },
		{ role: 'front', label: '' }
	]
};



